const useGapText = (createdAt: string): string => {
    const gap = Date.now() - Date.parse(createdAt);
    if (gap >= 604800000) {
        return `${Math.floor(gap / 604800000)} `;
    } else if (gap >= 86400000) {
        return `${Math.floor(gap / 86400000)} `;
    } else if (gap >= 3600000) {
        return `${Math.floor(gap / 3600000)} `;
    } else if (gap >= 60000) {
        return `${Math.floor(gap / 60000)} `;
    } else {
        return " ";
    }
};

export default useGapText;
